<template>
    <div class="details-mobile">
        <vue-scroll>
            <div class="content" v-if="isCreateVip === 1">
                <div class="head">
                    <p class="tit">{{ $L('我的信息') }}</p>
                    <div v-if="isHasCard === 0">
                        <div v-if="getCard === 1">
                            <span class="font-grey">{{ $L('你还有没有激活个人信息哟，请 ') }}</span>
                            <router-link :to="{name:'activeCard'}" class="mainFontColor"> {{ $L('立即激活') }}</router-link>
                        </div>
                        <div  class="right-tips mainFontColor"  v-else-if="getCard === -1">
                            <p class="text">{{ $L('联系商家开启会员卡！') }}</p>
                        </div>
                        <div  class="right-tips mainFontColor"  v-else-if="getCard === 0">
                            <p class="text">{{ priceGap == 0 ? $L('消费即赠送会员卡！'): $L('消费满')+priceGap +$L('赠送会员卡！') }}</p>
                        </div>
                    </div>
                    <div v-else>
                        <el-form :model="form" size="small" label-width="60px" label-position="left" class="info-msg">
                            <el-form-item v-for="(item,index) in form" :key="index" :label="item.name">
                                <el-input size="small" v-model="item.value" type="item.type" disabled/>
                            </el-form-item>
                        </el-form>
                    </div>
                </div>
                <div class="details">
                    <p class="tit">{{ $L('会员卡详情') }}</p>
                    <ul class="preview-text">
                        <li class="item-li">
                            <span class="item-span">{{ $L('享受折扣') }}</span>
                            <p class="item-p">{{ privilege.discount }}{{ $L('折') }}</p>
                        </li>
                        <li class="item-li">
                            <span class="item-span">{{ $L('特权说明') }}</span>
                            <p class="item-p">
                                <i v-if=" privilege.initBonus !== 0">{{ $L('激活成功即赠送') }}{{ privilege.initBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.costMoney !== 0">
                                    {{ $L('每消费') }}{{ privilege.costMoney }}{{ currencyCode }}，{{ $L('赠送') }}{{ privilege.incBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.maxBonus !== 0">{{ $L('单次赠送上限') }}{{ privilege.maxBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.costBonus !== 0">{{ $L('每使用') }}{{ privilege.costBonus }}{{ $L('积分') }}，{{ $L('抵扣') }}{{ privilege.reduceMoney }}{{ currencyCode }}<br></i>
                                <i v-if=" privilege.maxReduceBonus !== 0">{{ $L('订单满') }}{{ privilege.leastMoney }}{{ currencyCode }}{{ $L('可用积分抵扣 ，单笔使用上限') }}{{ privilege.maxReduceBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.discount !== 0">{{ $L('用卡可享受') }}{{ privilege.discount }}{{ $L('折优惠') }}<br></i>
                                <i v-if=" privilege.explain !== 0">{{ privilege.explain }}</i>
                            </p>
                        </li>
                        <li class="item-li">
                            <span class="item-span">{{ $L('有效日期') }}</span>
                            <p class="item-p" v-if="privilege.dateType == 1">{{ $L('永久有效') }}</p>
                            <p class="item-p" v-else>{{ privilege.beginTime +  $L('至')  + privilege.endTime}}</p>
                        </li>
                        <li class="item-li">
                            <span class="item-span">{{ $L('等级规则') }}</span>
                            <p class="item-p">
                                <i v-if=" privilege.costPayPoints !== 0">{{ $L('每消费') }}{{ privilege.costPayPoints }}{{ currencyCode }}{{ $L('赠送') }}
                                    {{ privilege.increaseRankPoints }}{{ $L('经验值') }}<br>
                                </i>
                                <i v-if=" privilege.maxRankPoints !== 0">{{ $L('单笔经验赠送上限为') }}{{ privilege.maxRankPoints }}<br></i>
                                <i v-if=" privilege.initRankPoints !== 0">{{ $L('首次激活卡赠送') }}{{ privilege.initRankPoints }}{{ $L('经验值') }}</i>
                            </p>
                        </li>
                    </ul>
                </div>
            </div>
            <div v-else class="no-data">
                {{ $L('商家未创建会员卡') }}
            </div>
        </vue-scroll>
    </div>
</template>

<script>
    export default {
        name: "detailsMobile",
        props : {
            routerData : {
                type : Object,
                default:()=>{
                    return {};
                }
            }
        },
        computed:{
            info:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('info')?this.routerData.data.info:{};
                }
            },
            privilege:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('info')?this.routerData.data.info.privilege:{};
                }
            },
            form:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('form')?this.routerData.data.form:{};
                }
            },
            isCreateVip:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('isCreateVip')?this.routerData.data.isCreateVip:'';
                }
            },
            isHasCard:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('isHasCard')?this.routerData.data.isHasCard:'';
                }
            },
            getCard:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('getCard')?this.routerData.data.getCard:'';
                }
            },
            priceGap:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('priceGap')?this.routerData.data.priceGap:'';
                }
            },
            currencyCode:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('currencyCode')?this.routerData.data.currencyCode:'';
                }
            },
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/css/common/common.scss";
    .details-mobile{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        .content{
            height: 100%;
            background: #fff;
            .head{
                margin: 20px;
                padding: 15px 0;
                border-bottom: 1px solid #eee;
                .tit{
                    padding-bottom: 10px;
                }
                .font-grey{
                    color: #999;
                }
                .info-msg{
                    /deep/ .el-form-item__label{
                        font-size: 12px;
                    }
                    /deep/ .el-form-item{
                        margin-bottom: 5px;
                    }
                    /deep/ .el-input__inner{
                        border: none;
                        background: #fff;
                    }
                }
            }
            .details{
                margin: 20px;
                padding: 15px 0;
                border-bottom: 1px solid #eee;
                .tit{
                    padding-bottom: 10px;
                }
                .preview-text{
                    .item-li{
                        clear: both;
                        margin-bottom: 10px;
                        .item-span{
                            color: #666;
                            margin-right: 15px;
                            float: left;
                            font-size: 12px;
                        }
                        .item-p{
                            line-height: 1.5;
                            display: block;
                            overflow: hidden;
                            span{
                                color: #666;
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }
        .right-tips{
            position: relative;
            height: 50px;
            font-size: 16px;
            background: #fff;
            .text{
                width: 100%;
                position: absolute;
                left: 50%;
                top: 50%;
                -webkit-transform: translateY(-50%);
                transform: translate(-50%,-50%);
            }
        }
        .no-data{
            padding: 0 10px;
            background: #fff;
            height: 50px;
            line-height: 50px;
            text-align: center;
            color: #999;
            margin-top: 10px;
        }
    }
</style>